// 
export function validateEmail(email) {
    // 
    if (!email || email.trim() === '') {
        return { isValid: true, message: '' };
    }
    // 
    const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
    if (emailRegex.test(email.trim())) {
        return { isValid: true, message: '' };
    }
    else {
        return { isValid: false, message: '' };
    }
}
// 
export function validatePhone(phone) {
    // 
    if (!phone || phone.trim() === '') {
        return { isValid: true, message: '' };
    }
    // 03-1234-5678, 090-1234-5678, 0312345678 
    const phoneRegex = /^(\d{2,4}-?\d{2,4}-?\d{4}|\d{10,11})$/;
    // 
    const cleanPhone = phone.replace(/-/g, '');
    if (phoneRegex.test(cleanPhone) && cleanPhone.length >= 10 && cleanPhone.length <= 11) {
        return { isValid: true, message: '' };
    }
    else {
        return { isValid: false, message: '' };
    }
}
// URL
export function validateWebsite(website) {
    // 
    if (!website || website.trim() === '') {
        return { isValid: true, message: '' };
    }
    try {
        // URLURL
        const url = new URL(website);
        // httphttps
        if (url.protocol === 'http:' || url.protocol === 'https:') {
            return { isValid: true, message: '' };
        }
        else {
            return { isValid: false, message: 'URL' };
        }
    }
    catch {
        return { isValid: false, message: 'URL' };
    }
}
// 
export function validateRequiredField(value, fieldName) {
    if (value === null || value === undefined || value.trim() === '') {
        return { isValid: false, message: `${fieldName}` };
    }
    return { isValid: true, message: '' };
}
// BusinessCardFormData
export function validateBusinessCardForm(formData) {
    const errors = {};
    // 
    const nameValidation = validateRequiredField(formData.name, '');
    if (!nameValidation.isValid) {
        errors.name = nameValidation.message;
    }
    const companyValidation = validateRequiredField(formData.company_name, '');
    if (!companyValidation.isValid) {
        errors.company_name = companyValidation.message;
    }
    // 
    if (formData.email !== undefined && formData.email !== null) {
        const emailValidation = validateEmail(formData.email);
        if (!emailValidation.isValid) {
            errors.email = emailValidation.message;
        }
    }
    if (formData.phone !== undefined && formData.phone !== null) {
        const phoneValidation = validatePhone(formData.phone);
        if (!phoneValidation.isValid) {
            errors.phone = phoneValidation.message;
        }
    }
    if (formData.website !== undefined && formData.website !== null) {
        const websiteValidation = validateWebsite(formData.website);
        if (!websiteValidation.isValid) {
            errors.website = websiteValidation.message;
        }
    }
    return {
        isValid: Object.keys(errors).length === 0,
        errors
    };
}
// 
export function validateUserName(name) {
    if (!name || name.trim() === '') {
        return { isValid: false, message: '' };
    }
    const trimmedName = name.trim();
    if (trimmedName.length < 2) {
        return { isValid: false, message: '2' };
    }
    if (trimmedName.length > 50) {
        return { isValid: false, message: '50' };
    }
    return { isValid: true, message: '' };
}
// 
export function validatePassword(password) {
    if (!password) {
        return { isValid: false, message: '' };
    }
    if (password.length < 6) {
        return { isValid: false, message: '6' };
    }
    if (password.length > 100) {
        return { isValid: false, message: '100' };
    }
    return { isValid: true, message: '' };
}
// 
export function validatePasswordConfirmation(password, confirmation) {
    if (!confirmation) {
        return { isValid: false, message: '' };
    }
    if (password !== confirmation) {
        return { isValid: false, message: '' };
    }
    return { isValid: true, message: '' };
}
// 
export function validateUserEmail(email) {
    if (!email || email.trim() === '') {
        return { isValid: false, message: '' };
    }
    // validateEmail
    return validateEmail(email);
}
// 
export function validateUserRegistration(formData) {
    const errors = {};
    const nameValidation = validateUserName(formData.name);
    if (!nameValidation.isValid) {
        errors.name = nameValidation.message;
    }
    const emailValidation = validateUserEmail(formData.email);
    if (!emailValidation.isValid) {
        errors.email = emailValidation.message;
    }
    const passwordValidation = validatePassword(formData.password);
    if (!passwordValidation.isValid) {
        errors.password = passwordValidation.message;
    }
    const passwordConfirmationValidation = validatePasswordConfirmation(formData.password, formData.passwordConfirmation);
    if (!passwordConfirmationValidation.isValid) {
        errors.passwordConfirmation = passwordConfirmationValidation.message;
    }
    return {
        isValid: Object.keys(errors).length === 0,
        errors
    };
}
