/**
 * 
 * React + Stimulus + Rails
 */
export class DebugHelper {
    static instance;
    debugContainer = null;
    static getInstance() {
        if (DebugHelper.instance === undefined) {
            DebugHelper.instance = new DebugHelper();
        }
        return DebugHelper.instance;
    }
    /**
     * 
     */
    showDebugPanel() {
        if (this.debugContainer !== null) {
            return;
        } // 
        this.debugContainer = document.createElement('div');
        this.debugContainer.id = 'debug-panel';
        this.debugContainer.className = 'mt-4 p-3 bg-info bg-opacity-10 rounded';
        this.debugContainer.innerHTML = `
      <h5> </h5>
      <p id="debug-status"></p>
      <div class="btn-group" role="group">
        <button onclick="window.DebugHelper.testJavaScript()" class="btn btn-sm btn-outline-info"></button>
        <button onclick="window.DebugHelper.debugStimulus()" class="btn btn-sm btn-outline-warning">Stimulus</button>
        <button onclick="window.DebugHelper.debugReact()" class="btn btn-sm btn-outline-success">React</button>
        <button onclick="window.DebugHelper.hideDebugPanel()" class="btn btn-sm btn-outline-danger"></button>
      </div>
      <div id="debug-details" class="mt-3" style="display: none;">
        <pre id="debug-output" class="bg-dark text-light p-2 rounded" style="font-size: 12px; max-height: 300px; overflow-y: auto;"></pre>
      </div>
    `;
        document.body.appendChild(this.debugContainer);
        console.log(" ");
    }
    /**
     * 
     */
    hideDebugPanel() {
        if (this.debugContainer) {
            this.debugContainer.remove();
            this.debugContainer = null;
            console.log(" ");
        }
    }
    /**
     * JavaScript
     */
    testJavaScript() {
        const status = document.getElementById('debug-status');
        if (status === null) {
            return;
        }
        let result = " JavaScript\n";
        console.log("=== JavaScript ===");
        // Stimulus
        if (typeof window.Stimulus !== 'undefined') {
            result += " Stimulus\n";
            console.log(" Stimulus found:", window.Stimulus);
        }
        else {
            result += " Stimulus\n";
            console.log(" Stimulus not found");
        }
        // React
        if (typeof window.React !== 'undefined') {
            result += " React\n";
            console.log(" React found:", window.React);
        }
        else {
            result += " React\n";
            console.log(" React not found");
        }
        status.innerHTML = result.replace(/\n/g, '<br>');
        this.updateDebugOutput("");
    }
    /**
     * Stimulus
     */
    debugStimulus() {
        console.log("=== Stimulus ===");
        const stimulus = window.Stimulus;
        if (stimulus === undefined) {
            this.updateDebugOutput(" Stimulus");
            return;
        }
        let output = "=== Stimulus ===\n";
        output += `: ${stimulus.constructor.name}\n`;
        output += `: ${stimulus.debug}\n`;
        // 
        const modules = stimulus.router?.modulesByIdentifier ?? {};
        output += `\n (${Object.keys(modules).length}):\n`;
        Object.keys(modules).forEach(identifier => {
            output += `  - ${identifier}\n`;
        });
        // DOM
        const controllerElements = document.querySelectorAll('[data-controller]');
        output += `\nDOM (${controllerElements.length}):\n`;
        controllerElements.forEach((element, index) => {
            const controllers = element.getAttribute('data-controller');
            output += `  ${index + 1}. ${controllers} (${element.tagName.toLowerCase()})\n`;
        });
        this.updateDebugOutput(output);
    }
    /**
     * React
     */
    debugReact() {
        console.log("=== React ===");
        const React = window.React;
        if (React === undefined) {
            this.updateDebugOutput(" React");
            return;
        }
        let output = "=== React ===\n";
        output += `: ${React.version}\n`;
        // React
        const reactElements = document.querySelectorAll('[data-reactroot]');
        output += `\nReact (${reactElements.length}):\n`;
        reactElements.forEach((element, index) => {
            const reactRoot = element.getAttribute('data-reactroot');
            output += `  ${index + 1}. ${element.tagName.toLowerCase()}`;
            if (reactRoot !== null) {
                output += ` (reactroot)`;
            }
            output += `\n`;
        });
        this.updateDebugOutput(output);
    }
    /**
     * Stimulus
     */
    debugStimulusController(controllerName) {
        console.log(`=== ${controllerName} ===`);
        const element = document.querySelector(`[data-controller*="${controllerName}"]`);
        if (!element) {
            this.updateDebugOutput(` ${controllerName}`);
            return;
        }
        let output = `=== ${controllerName} ===\n`;
        output += `: ${element.tagName.toLowerCase()}\n`;
        output += `: ${element.getAttribute('data-controller')}\n`;
        // data-* 
        const attributes = Array.from(element.attributes)
            .filter(attr => attr.name.startsWith('data-'))
            .map(attr => `  ${attr.name}: ${attr.value}`)
            .join('\n');
        if (attributes) {
            output += `\ndata-:\n${attributes}\n`;
        }
        // JSON
        const valueAttributes = Array.from(element.attributes)
            .filter(attr => attr.name.includes('-value'));
        if (valueAttributes.length > 0) {
            output += `\nJSON:\n`;
            valueAttributes.forEach(attr => {
                try {
                    const parsed = JSON.parse(attr.value ?? '{}');
                    output += `   ${attr.name}: ${JSON.stringify(parsed, null, 2)}\n`;
                }
                catch (error) {
                    output += `   ${attr.name}: JSON - ${error}\n`;
                }
            });
        }
        this.updateDebugOutput(output);
    }
    /**
     * 
     */
    updateDebugOutput(content) {
        const details = document.getElementById('debug-details');
        const output = document.getElementById('debug-output');
        if (details && output) {
            details.style.display = 'block';
            output.textContent = content;
            console.log(content);
        }
    }
}
// 
export function initializeDebug() {
    window.DebugHelper = DebugHelper;
    // Ctrl+D /
    document.addEventListener('keydown', (event) => {
        if (event.ctrlKey && event.key === 'd') {
            event.preventDefault();
            const debugHelper = DebugHelper.getInstance();
            if (document.getElementById('debug-panel')) {
                debugHelper.hideDebugPanel();
            }
            else {
                debugHelper.showDebugPanel();
            }
        }
    });
    console.log("  (Ctrl+D /)");
}
